<template>
  <div>
    <v-container fluid>
      <v-row align="center" justify="center" class="ma-0">
        <v-col class="pa-0" cols="12" md="6" lg="6" xl="6">
          <h3>HTML Editor</h3>
          <div class="codemirror" style="padding:0px 2px">
            <!-- codemirror -->
            <codemirror v-model="code" :options="cmOption" ref="cm" id="cm"></codemirror>
            <div style="margin-top:8px">
              <v-btn v-on:click="run" color="primary">run</v-btn>
            </div> 
          </div>
                   
        </v-col>
        <v-col id="col-right" class="pa-0" cols="12" md="6" lg="6" xl="6">
          <div style="padding:0px 2px">
            <h3>Result</h3>
            <iframe
              src="about:blank"
              frameborder="1"
              width="100%"
              id="myIframe"
            ></iframe>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
/* eslint-disable */
// language
import "codemirror/mode/javascript/javascript.js";
import "codemirror/mode/htmlmixed/htmlmixed.js";
// theme css
import "codemirror/theme/monokai.css";

// require active-line.js
import "codemirror/addon/selection/active-line.js";
// styleSelectedText
import "codemirror/addon/selection/mark-selection.js";
import "codemirror/addon/search/searchcursor.js";
// hint
import "codemirror/addon/hint/show-hint.js";
import "codemirror/addon/hint/show-hint.css";
import "codemirror/addon/hint/javascript-hint.js";
import "codemirror/addon/selection/active-line.js";
// highlightSelectionMatches
import "codemirror/addon/scroll/annotatescrollbar.js";
import "codemirror/addon/search/matchesonscrollbar.js";
import "codemirror/addon/search/searchcursor.js";
import "codemirror/addon/search/match-highlighter.js";
// keyMap
import "codemirror/mode/clike/clike.js";
import "codemirror/addon/edit/matchbrackets.js";
import "codemirror/addon/comment/comment.js";
import "codemirror/addon/dialog/dialog.js";
import "codemirror/addon/dialog/dialog.css";
import "codemirror/addon/search/searchcursor.js";
import "codemirror/addon/search/search.js";
import "codemirror/keymap/sublime.js";
// foldGutter
import "codemirror/addon/fold/foldgutter.css";
import "codemirror/addon/fold/brace-fold.js";
import "codemirror/addon/fold/comment-fold.js";
import "codemirror/addon/fold/foldcode.js";
import "codemirror/addon/fold/foldgutter.js";
import "codemirror/addon/fold/indent-fold.js";
import "codemirror/addon/fold/markdown-fold.js";
import "codemirror/addon/fold/xml-fold.js";

export default {
  data() {
    return {
      code: `<!DOCTYPE html>
<html>
<head></head>

<body>

</body>

</html>`,
      cmOption: {
        tabSize: 4,
        styleActiveLine: false,
        lineNumbers: true,
        styleSelectedText: false,
        line: true,
        foldGutter: true,
        gutters: ["CodeMirror-linenumbers", "CodeMirror-foldgutter"],
        highlightSelectionMatches: { showToken: /\w/, annotateScrollbar: true },
        mode: "text/html",
        // hint.js options
        hintOptions: {
          completeSingle: false
        },
        //keymap: sublime、emacs、vim
        keyMap: "sublime",
        matchBrackets: true,
        showCursorWhenSelecting: true,
        theme: "monokai",
        extraKeys: { Ctrl: "autocomplete" }
      },
      iframe: null,
      idoc: null,
      clientHeight: document.body.clientHeight
    };
  },
  mounted() {
    setTimeout(() => {
      (this.styleSelectedText = true), (this.cmOption.styleActiveLine = true);
    }, 1800);

    //get the height of browser window
    this.clientHeight = `${document.documentElement.clientHeight}`; //document.body.clientWidth;
    this.clientHeight -= 56; //subtract height of app bar
    //console.log(this.clientHeight);
    this.setHeight();

    //dynamic change the height of the editor
    window.onresize = () => {
      this.clientHeight = `${document.documentElement.clientHeight}`;
      this.clientHeight -= 56; //subtract height of app bar
      this.setHeight();
    };

    this.iframe = document.getElementById("myIframe");
  },
  methods: {
    run() {
      this.iframe.srcdoc = this.$refs.cm.codemirror.getValue();
    },
    getHeight() {
      //subtract height of h3(28px), buttons div(46px) and v-container's padding(24px)
      return this.clientHeight - 98;
    },
    setHeight() {
      document.getElementById("myIframe").style.height = this.getHeight() + "px";
      document.getElementById("col-right").style.height = this.clientHeight - 24 + "px";
      this.$refs.cm.codemirror.setSize(null, this.getHeight());
    }
  }
};
/* eslint-disable */
</script>

